
import React from 'react';
import T from 'i18n-react'
import { Select } from 'bloomer';
import "../assets/styles/float-label-input.css";


const FloatLabelInput = ({ onChange, ...rest }) => {
    return (
        <main className={`control float-label-input but-select`}>
            <Select {...rest} onChange={onChange}>
                {rest.children}
            </Select>
            <label>{rest.label}</label>
            
        </main>
    );
}

export default FloatLabelInput;



