import React, { Component } from 'react';
import T from 'i18n-react'
import { Link } from 'react-router-dom';
import { Hero, HeroBody, HeroFooter, Container, Box, Columns, Column, Image, Button } from "bloomer";
import FloatLabelnput from "../components/FloatLabelnput";
import Footer from "../components/Footer";
import alertify from 'alertify.js'

import recaptchaconf from '../config/recaptcha.json'

import ReCaptcha from "react-google-recaptcha"

import cap, { Redir } from '../lib/capsdk'

import globalStorage from 'react-global-storage';



import "../assets/styles/page-login.css";

import logo from "../assets/images/SVG/capitual-logo.svg";



class Recover extends Component {

    state = {
        user: "",
        redir: ""
    }

    componentDidMount = () => {
        globalStorage.setState('userID', false);
    }

    recoverPwd = async(e) => {
        e.preventDefault()
        if (this.state.user) {
            try {
                let res = await cap.requestA('members.recoverPassword', {
                    email_or_phone: this.state.user
                })

                if (res.status === 'ok') {
                    if (res.method=='email') {
                        await alertify.okBtn(T.translate("global.labels.ok")).alert(T.translate("pages.recover.sentByEmail"))
                    }
                    else if (res.method== 'sms') {
                        await alertify.okBtn(T.translate("global.labels.ok")).alert(T.translalte("pages.recover.sentBySms"))
                    }
                    this.setState({
                        redir: "/recovered?email="+encodeURIComponent(this.state.user)
                    })
                }
                else if (res.status === 'MultipleMethods') {
                    let continueBy = await alertify
                                        .okBtn(T.translate("pages.recover.sendByEmail"))
                                        .cancelBtn(T.translate("pages.recover.sendBySms"))
                                        .confirm(T.translate("pages.recover.sendBy"))
                    
                    this.setState({
                        sendTokenBy: continueBy.buttonClicked === 'ok' ? 'email' : 'sms'
                    })

                    cap.requestA('members.recoverPassword', {
                        email_or_phone: this.state.user,
                        sendTokenBy: this.state.sendTokenBy
                    })

                    if (this.state.sendTokenBy=='email') {
                        await alertify.reset().alert(T.translate("pages.recover.sentByEmail"))
                    }
                    else if (this.state.sendTokenBy== 'sms') {
                        await alertify.reset().alert(T.translate("pages.recover.sentBySms"))
                    }
                    this.setState({
                        redir: "/recovered?email="+encodeURIComponent(this.state.user)
                    })
                }
            } catch(e) {
                switch (e) {
                    case 'InvalidUsername':
                        this.setState({isUsernameError: true})
                        break
                    case 'MustConfirmAccount':
                        alertify.okBtn(T.translate("global.labels.ok")).alert(T.translate("pages.recover.noPwdYet"))
                        break
                    case 'MustConfirmUseEmail':
                        alertify.okBtn(T.translate("global.labels.ok")).alert(T.translate("pages.recover.noPwdYetUsePrimaryEmail"))
                        break
                } 
            }
        }
    }

    render() {
        return (
            <Hero className="page-login" isFullHeight>
                <ReCaptcha sitekey={ recaptchaconf.sitekey } onChange={ val => this.setState({ recaptcha_result: val }) } size="invisible" badge="inline" />
                <Redir to={this.state.redir} />
                <HeroBody>
                    <Container>
                        <form onSubmit={this.recoverPwd}>
                            <Columns isCentered>
                                <Column isSize={4} isMarginless>
                                    <Box>
                                        <Columns isMultiline>
                                            <Column isSize="auto">
                                                <Link to="/">
                                                    <Image src={logo} isSize="64x64" hasTextAlign="centered" style={{margin: '0 auto'}} />
                                                </Link>
                                            </Column>
                                            <br/>
                                            <Column isSize={12}>
                                                <FloatLabelnput label={ T.translate("pages.recover.input") }  isColor={this.state.isUsernameError ? 'danger' : undefined}  helpText={this.state.isUsernameError && T.translate("pages.recover.usernameError")} onChange={(e) => this.setState({ user: e.target.value })} required />
                                            </Column>
                                            
                                            <Column isSize={12}>
                                                <Button type="submit" isColor="primary" isSize="large" isFullWidth>{ T.translate("pages.recover.submit") }</Button>
                                            </Column>
                                            <Column>
                                                <p className="has-text-centered"><Link to="/login">{ T.translate("pages.recover.signIn") }</Link></p>
                                            </Column>
                                        </Columns>
                                    </Box>
                                </Column>
                            </Columns>
                        </form>
                    </Container>
                </HeroBody>
                <HeroFooter>
                    <Footer />
                </HeroFooter>
            </Hero>
        );
    }
}

export default Recover;


